import Matter, { Bodies, Body, Engine, World } from "matter-js";
import React, { useEffect, useRef } from "react";
import "./App.css";

type Vector = {
  x: number;
  y: number;
};

const getGravitationalForce = (
  mass1: number,
  mass2: number,
  distance: number
) => {
  const force = (0.01 * mass1 * mass2) / (distance * Math.sqrt(distance + 0.15));
  return force;
};

const getDistance = (position1: Vector, position2: Vector) => {
  var a = Math.abs(position1.x - position2.x);
  var b = Math.abs(position1.y - position2.y);
  return Math.sqrt(Math.pow(a, 2) + Math.pow(b, 2));
};

const getDirection = (position1: Vector, position2: Vector) => {
  return Math.atan2(position1.x - position2.x, position1.y - position2.y);
};

const drawCircle = (
  ctx: CanvasRenderingContext2D,
  position: Matter.Vector,
  radius: number,
  color: string
) => {
  ctx.fillStyle = color;
  ctx.beginPath();
  ctx.arc(position.x, position.y, radius, 0, Math.PI * 2);
  ctx.closePath();
  ctx.fill();
};
const drawPolygon = (
  ctx: CanvasRenderingContext2D,
  position: Vector,
  vertices: Matter.Vector[],
  color: string
) => {
  ctx.fillStyle = color;
  ctx.beginPath();
  for (let i = 0; i < vertices.length; i++) {
    const { x, y } = vertices[i];
    if (!i) {
      ctx.moveTo(x, y);
    } else {
      ctx.lineTo(x, y);
    }
  }
  ctx.closePath();
  ctx.fill();
};

const drawBody = (ctx: CanvasRenderingContext2D, body: Matter.Body) => {
  if (body.circleRadius) {
    drawCircle(ctx, body.position, body.circleRadius, body.render.fillStyle!);
  } else if (body.vertices) {
    drawPolygon(ctx, body.position, body.vertices, body.render.fillStyle!);
  }
};

const drawLine = (
  ctx: CanvasRenderingContext2D,
  startPosition: Vector,
  endPosition: Vector,
  color: string
) => {
  ctx.strokeStyle = color;
  ctx.beginPath();
  ctx.moveTo(startPosition.x, startPosition.y);
  ctx.lineTo(endPosition.x, endPosition.y);
  ctx.stroke();
};

function App() {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);

  useEffect(() => {
    const canvas = canvasRef.current!;

    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;

    const ctx = canvas.getContext("2d");

    if (!ctx) {
      throw new Error(`Context 2d could not be retrieved`);
    }

    let mdBody: Matter.Body | null;
    let mmPosition: Vector | null;

    var engine = Engine.create({ gravity: { x: 0, y: 0 } });

    canvas.addEventListener("mousedown", (mdEvent) => {
      let mass;
      let radius;

      if (mdEvent.shiftKey) {
        mass = Math.random() * 1000 + 500;
        radius = mass / 50;
      } else {
        mass = Math.random() * 10 + 5;
        radius = mass / 2;
      }

      mdBody = Bodies.circle(mdEvent.offsetX, mdEvent.offsetY, radius, {
        mass,
        isStatic: mdEvent.ctrlKey,
        render: {
          fillStyle: "#" + Math.floor(Math.random() * 16777215).toString(16),
        },
        friction: 1,
        frictionAir: 0,
        restitution: 0,
      });

      const mmHandler = (mmEvent: MouseEvent) => {
        mmPosition = { x: mmEvent.offsetX, y: mmEvent.offsetY };
      };

      const muHandler = (muEvent: MouseEvent) => {
        if (!mdBody) {
          throw new Error(`PRotu`);
        }
        const mdPosition = mdBody.position;

        if (mmPosition) {
          Body.applyForce(mdBody, mdBody.position, {
            x: (mdPosition.x - muEvent.offsetX) / 1000,
            y: (mdPosition.y - muEvent.offsetY) / 1000,
          });
        }

        World.addBody(engine.world, mdBody);

        mdBody = null;
        mmPosition = null;

        canvas.removeEventListener("mousemove", mmHandler);

        canvas.removeEventListener("mouseup", muHandler);
      };

      canvas.addEventListener("mousemove", mmHandler);

      canvas.addEventListener("mouseup", muHandler);
    });

    const loop = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      for (const body of engine.world.bodies) {
        drawBody(ctx, body);

        if (body.isStatic) {
          continue;
        }

        for (const otherBody of engine.world.bodies) {
          if (body === otherBody) {
            continue;
          }

          const distance = getDistance(otherBody.position, body.position);

          const forceDirection = getDirection(
            otherBody.position,
            body.position
          );
          const forceMagnitude = getGravitationalForce(
            otherBody.mass,
            body.mass,
            distance
          );

          Body.applyForce(body, body.position, {
            x: (Math.sin(forceDirection) * forceMagnitude) / body.mass,
            y: (Math.cos(forceDirection) * forceMagnitude) / body.mass,
          });
        }
      }

      if (mdBody) {
        if (mdBody.circleRadius) {
          drawCircle(
            ctx,
            mdBody.position,
            mdBody.circleRadius!,
            mdBody.render.fillStyle!
          );
        } else if (mdBody.vertices) {
          drawPolygon(
            ctx,
            mdBody.position,
            mdBody.vertices,
            mdBody.render.fillStyle!
          );
        }

        if (mmPosition) {
          drawLine(ctx, mdBody.position, mmPosition, mdBody.render.fillStyle!);
        }
      }

      Engine.update(engine);

      requestAnimationFrame(loop);
    };

    loop();
  }, []);

  return (
    <>
      <canvas ref={canvasRef} />
      <div className="overlay">
        <span>Click to create a planet</span>
        <span>Move the mouse before releasing the click to throw a planet</span>
        <span>
          Hold <kbd>Ctrl</kbd> to create a static planet
        </span>
        <span>
          Hold <kbd>Shift</kbd> to create a big planet
        </span>
      </div>
    </>
  );
}

export default App;
